<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}">
      <div style="padding-top: 1rem;">
        <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
          <nav-head :title="title" ></nav-head>
        </div>
        <div class="fixed-box">
        <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
          <nav-head :title="title" ></nav-head>
        </div>
        <!--隐藏原型图上的内容 最新公告 公告中心 公告中心-->
        <!-- <div class="gg-tab">
          <span :class="isSelect == 0?'gg-tab-active':''" @click="userSelect(0)" class="">最新公告</span>
          <span :class="isSelect == 1?'gg-tab-active':''" @click="userSelect(1)">公告中心</span>
          <span :class="isSelect == 2?'gg-tab-active':''" @click="userSelect(2)">帮助中心</span>
          <div class="gg-tab-img-box">
          </div>
        </div> -->
        <!--      分割线-->
        <div class="dividing-line"></div>
        </div>
        <div class="zhanwei"></div>
        <div class="list-item">
          <p class="title">{{newsdetailsCon.title}}</p>
          <span class="time-box">{{ newsdetailsCon.create_time?format(newsdetailsCon.create_time):''}}</span>
        </div>
        <div class="html-area" >
          <div class="contentText" v-html="newsdetailsCon.content"></div>
          <!-- <p>内容区域爱搜代购发收款方光辉拿手机分期WPS分隔</p> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Search, Icon } from 'vant'
import { newsdetails } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Icon)
Vue.use(Search)
export default {
  components: { NavHead },
  data () {
    return {
      curHeight: '',
      newsdetailsData: {
        type: 'news',
        news_id: ''
      },
      newsdetailsCon: [], // 详情内容
      title: '公告明细',
      isSelect: 0
    }
  },
  created () {
    this.newsdetailsData.news_id = this.$route.query.news_id
    this.beforeMount()
    this.newsdetailsFun()
  },
  methods: {
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d) + '  ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
    },
    newsdetailsFun () {
      var that = this
      var data = objKeySort(this.newsdetailsData)
      var token = sgin(data)
      this.newsdetailsData.token = token
      newsdetails(this.newsdetailsData).then(res => {
        console.log(res, that, '新闻列表详情')
        if (res.code === 0) {
          that.newsdetailsCon = res.data
        }
      }).catch((err) => {
        console.log(err, that)
        delete this.newsdetailsData.token
      })
    },
    userSelect (type) {
      this.isSelect = type
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    }
  }
}
</script>
